import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { doc, getDoc } from 'firebase/firestore';
import { db } from '../../services/firebase/firebaseConfig';
import {
	Grid,
	TableContainer,
	Table,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Typography,
	Paper,
} from '@mui/material';
import { Link } from 'react-router-dom';
import { tableStyle } from './styles';

const Game = () => {
	let params = useParams();
	const [game, setGame] = useState(null);
	const [error, setError] = useState(null);
	const getGame = async () => {
		const docRef = doc(db, 'games', `${params.gameID}`);
		console.log(`${params}`);
		const docSnap = await getDoc(docRef);

		if (docSnap.exists()) {
			setGame(docSnap.data());
		} else {
			setError('No such document!');
		}
	};

	useEffect(() => {
		getGame();
	}, []);

	if (error) {
		return (
			<div>
				<p>{error}</p>
			</div>
		);
	}

	if (!game) {
		return (
			<div>
				<p>Loading...</p>
			</div>
		);
	}

	console.log(game);
	return (
		<div>
			<Grid container spacing={2}>
				<Grid item xs={12}>
					<Typography variant='body1' component='p'>
						Location: {game.location}
					</Typography>
					<Typography variant='body1' component='p'>
						Date of game:{' '}
						{new Date(game.date_of_game.toMillis()).toLocaleDateString('en-US')}
					</Typography>
				</Grid>
				<Grid item xs={12}>
					<TableContainer component={Paper}>
						<Table>
							<TableHead>
								<TableRow>
									<TableCell>Player Name</TableCell>
									<TableCell>Points</TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								{game.players.map((player) => {
									return (
										<TableRow key={player.player_id}>
											<TableCell sx={tableStyle}>
												<Link to={`/player/${player.player_id}`}>
													{player.player_name}
												</Link>
											</TableCell>
											<TableCell>{player.player_points}</TableCell>
										</TableRow>
									);
								})}
							</TableBody>
						</Table>
					</TableContainer>
				</Grid>
			</Grid>
		</div>
	);
};

export default Game;
