import React, { useEffect, useState } from 'react';
import { Box } from '@mui/material';
import {
	collection,
	serverTimestamp,
	onSnapshot,
	orderBy,
	deleteDoc,
	doc,
	query,
} from 'firebase/firestore';
import { db } from '../../services/firebase/firebaseConfig';
import PlayersLogic from './PlayersLogic/PlayersLogic';
import GamesLogic from './GamesLogic/GamesLogic';

const PGLogic = () => {
	const [players, setPlayers] = useState();
	const [games, setGames] = useState();

	const allPlayers = async () => {
		try {
			const q = await query(collection(db, 'players'));
			const unsub = await onSnapshot(q, (snapshot) => {
				setPlayers(
					snapshot.docs.map((doc) => ({
						id: doc.id,
						player: doc.data(),
					}))
				);
			});
			return unsub;
		} catch (error) {
			console.log(error);
		}
	};

	const allGames = async () => {
		try {
			const q = await query(collection(db, 'games'));
			const unsub = await onSnapshot(q, (snapshot) => {
				setGames(
					snapshot.docs.map((doc) => ({
						id: doc.id,
						game: doc.data(),
					}))
				);
			});
			return unsub;
		} catch (error) {
			console.log(error);
		}
	};

	useEffect(() => {
		allPlayers();
	}, []);

	useEffect(() => {
		allGames();
	}, []);

	console.log(players);
	return (
		<Box component='div'>
			<Box component='div'>
				{players ? <PlayersLogic players={players} /> : ''}
			</Box>
			<Box component='div'>{games ? <GamesLogic games={games} /> : ''}</Box>
		</Box>
	);
};

export default PGLogic;
