import React from 'react';
import PropTypes from 'prop-types';
import {
	Button,
	TableContainer,
	TableHead,
	Table,
	TableRow,
	TableCell,
	TableBody,
	Paper,
} from '@mui/material';
import {
	plyersTable,
	buttonStyle,
	linkStyle,
} from '../../PGLogic/PlayersLogic/styles';
import { Link } from 'react-router-dom';

const PlayerLogicAdmin = ({ players, handlePlayerEdit }) => {
	return (
		<TableContainer component={Paper}>
			<Table aria-label='simple table' sx={plyersTable}>
				<TableHead>
					<TableRow>
						<TableCell>Name</TableCell>
						<TableCell>Edit/Delete</TableCell>
					</TableRow>
				</TableHead>
				<TableBody>
					{players.map((player) => {
						return (
							<TableRow key={player.id}>
								<TableCell sx={linkStyle}>
									<Link to={`/player/${player.id}`}>{player.player.name}</Link>
								</TableCell>

								<TableCell>
									<Button
										variant='contained'
										sx={buttonStyle}
										onClick={(event) => handlePlayerEdit(player.id)}
									>
										edit
									</Button>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

PlayerLogicAdmin.propTypes = {
	players: PropTypes.any.isRequired,
	handlePlayerEdit: PropTypes.func.isRequired,
};

export default PlayerLogicAdmin;
