import React from 'react';
import PropTypes from 'prop-types';
import {
	TableContainer,
	TableHead,
	Table,
	TableRow,
	TableCell,
	TableBody,
	Paper,
} from '@mui/material';
import { plyersTable, buttonStyle, linkStyle } from './styles';
import { Link } from 'react-router-dom';

const PlayersLogic = ({ players }) => {
	return (
		<TableContainer component={Paper}>
			<Table aria-label='simple table' sx={plyersTable}>
				<TableHead>
					<TableRow>
						<TableCell>Name</TableCell>
						<TableCell>Points</TableCell>
					</TableRow>
				</TableHead>
				<TableBody>
					{players.map((player) => {
						return (
							<TableRow key={player.id}>
								<TableCell sx={linkStyle}>
									<Link to={`/player/${player.id}`}>{player.player.name}</Link>
								</TableCell>
								<TableCell></TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

PlayersLogic.propTypes = {
	players: PropTypes.any.isRequired,
};

export default PlayersLogic;
