import React, { useEffect, useState } from 'react';
import { Box, Button } from '@mui/material';
import {
	collection,
	serverTimestamp,
	onSnapshot,
	orderBy,
	deleteDoc,
	doc,
	query,
} from 'firebase/firestore';
import { db } from '../../services/firebase/firebaseConfig';
import PlayerLogicAdmin from './PlayerLogicAdmin/PlayerLogicAdmin';
import GamesLogicAdmin from './GamesLogicAdmin/GamesLogicAdmin';
import { useNavigate } from 'react-router-dom';
import { buttonStyle } from './styles';

const PGLogicAdmin = () => {
	const [players, setPlayers] = useState();
	const [games, setGames] = useState();
	let navigate = useNavigate();

	const allPlayers = async () => {
		try {
			const q = await query(collection(db, 'players'));
			const unsub = await onSnapshot(q, (snapshot) => {
				setPlayers(
					snapshot.docs.map((doc) => ({
						id: doc.id,
						player: doc.data(),
					}))
				);
			});
			return unsub;
		} catch (error) {
			console.log(error);
		}
	};

	const allGames = async () => {
		try {
			const q = await query(collection(db, 'games'));
			const unsub = await onSnapshot(q, (snapshot) => {
				setGames(
					snapshot.docs.map((doc) => ({
						id: doc.id,
						game: doc.data(),
					}))
				);
			});
			return unsub;
		} catch (error) {
			console.log(error);
		}
	};

	const deleteGames = async (gamesId) => {
		try {
			const gamesDoc = doc(db, 'games', gamesId);
			await deleteDoc(gamesDoc);
		} catch (error) {
			console.log(error);
		}
	};

	const handlePlayerEdit = (id) => {
		navigate(`${id}`);
	};

	const handlePlayerNew = () => {
		navigate('newplayer');
	};

	useEffect(() => {
		allPlayers();
	}, []);

	useEffect(() => {
		allGames();
	}, []);

	console.log(players);
	return (
		<Box component='div'>
			{/* <Box component='div'>
				<Button variant='contained' sx={buttonStyle} onClick={handlePlayerNew}>
					New Player
				</Button>
			</Box> */}
			<Box component='div' mt={5}>
				<Button variant='contained' onClick={handlePlayerNew} sx={buttonStyle}>
					New Player
				</Button>
				{players ? (
					<PlayerLogicAdmin
						players={players}
						handlePlayerEdit={handlePlayerEdit}
					/>
				) : (
					''
				)}
			</Box>
			<Box component='div'>
				{games ? (
					<GamesLogicAdmin games={games} deleteGames={deleteGames} />
				) : (
					''
				)}
			</Box>
		</Box>
	);
};

export default PGLogicAdmin;
