import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { CssBaseline, Container } from '@mui/material';
import ScrollToTop from './components/ScrollToTop/ScrollToRop';
import {
	Home,
	AdminDashboard,
	LogIn,
	Signup,
	NavigationBar,
	Player,
	Game,
	NotFound,
	EditPlayer,
	NewPlayer,
} from './pages';
import PrivateRout from './services/auth/PrivateRout';
import { AuthProvider } from './hooks/AuthContext';

const App = () => {
	const handleSubmit = async (e) => {
		e.preventDefault();
		console.log('cao');
	};
	const a = '';

	return (
		<Router>
			<CssBaseline />
			<ScrollToTop />
			<AuthProvider>
				<NavigationBar />
				<Container>
					<Routes>
						<Route path='/' element={<Home />} />
						{/* <Route path='/signup' element={<Signup />} /> */}
						<Route path='loginadimin' element={<LogIn />} />
						<Route path='player'>
							<Route path=':playerID' element={<Player />} />
							<Route path='*' element={<NotFound />} />
						</Route>
						<Route path='game'>
							<Route path=':gameID' element={<Game />} />
							<Route path='*' element={<NotFound />} />
						</Route>
						<Route path='admindashboard' element={<PrivateRout />}>
							<Route path='' element={<AdminDashboard />} />
							<Route path=':editplayerID' element={<EditPlayer />} />
							<Route path='newplayer' element={<NewPlayer />} />
						</Route>
						<Route path='*' element={<NotFound />} />
					</Routes>
				</Container>
			</AuthProvider>
		</Router>
	);
};

export default App;
